import { ModalController } from 'ionic-angular';
import { DEFAULT_LOGO } from './../../../services/constants';
import { CacheProvider } from './../../../providers/cache-provider';
import { Clipboard } from '@ionic-native/clipboard';
import { WidgetProvider } from './../../../providers/widget-provider'; 
import { Message, } from '../../../models/models';
import { PreloadProvider } from './../../../providers/preload-provider';
import { Keyboard } from '@ionic-native/keyboard';
import { NativeService } from './../../../services/native-service';

import { StorageService } from './../../../services/storage-service';
import { DomSanitizer } from '@angular/platform-browser';
import { Utility } from './../../../services/utility';
import { MessageProvider } from './../../../providers/message-provider';
import { Session } from './../../../services/session';
import { FormControl, FormBuilder } from '@angular/forms';
import { Component, ViewChild, ElementRef, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, Content, NavParams, Events, AlertController, ActionSheetController } from 'ionic-angular';
import { Webpage } from '../../../models/models';
import { TranslateService } from '@ngx-translate/core';

/**
 * Generated class for the MessageChatPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@IonicPage()
@Component({
  selector: 'page-message-chat',
  templateUrl: 'message-chat.html',
})
export class MessageChatPage {
 
  limit: number = 15; //加载消息条数
  offset: number = 0; //偏移量
  toUser = {
    _id: 'initialId',
    pic: 'assets/imgs/logo.png',
    username: 'reveiceUser',
  };

  user = {
    _id: 'initialId',
    pic: 'assets/imgs/logo.png',
    username: 'currentUser',
  };

  isShowChooser: boolean = false; //输入时是否显示更多选项  

  isSending: boolean = false; 
  pending: any; 
  messages = []; //当前信息
  messageTitle: string  //session Room标题
  widgetId: string; //对方id
  isTyping: boolean = false;
  topId: string = '' // 最顶消息id，记录scroll位置用 
  @ViewChild(Content) content: Content;
  @ViewChild('inputId') myInput; //输入框element
  @ViewChild('footer') footer;//footer element 
  public messageForm: any;
  chatBox: any; //输入框Model 
  // isTyping: boolean = false; //是否正在输入
  constructor(public navParams: NavParams,
    public navCtrl: NavController,
    public formBuilder: FormBuilder,
    public session: Session,
    public messageProvider: MessageProvider,
    private sanitizer: DomSanitizer,
    public storageService: StorageService,
    public element: ElementRef,
    public nativeService: NativeService,
    public keyboard: Keyboard,
    public preloadProvider: PreloadProvider, 
    public events: Events,
    public alertCtrl:AlertController,
    public translateService:TranslateService,
    public widgetProvider: WidgetProvider,
    public clipboard: Clipboard,
    public preload: PreloadProvider,
    public cacheProvider: CacheProvider, 
    public actionSheetCtrl: ActionSheetController,
    private modal: ModalController,
    private cd: ChangeDetectorRef) {
   
 

      
    this.messageForm = formBuilder.group({
      message: new FormControl('')
    });
    
    // this.widgetId = this.navParams.data.widgetId;
    // this.pending = this.navParams.data.pending;
    this.keyboard.onKeyboardWillShow().subscribe((e: any) => { 
      if(this.nativeService.isAndroid()){ 
        this.setFooterPosition(this.session.keyboardHeight || 243) 
      }
      this.ContentResize()
    }); 

    this.keyboard.onKeyboardWillHide().subscribe((e: any) => {  
      if(this.nativeService.isAndroid()){ 
        this.setFooterPosition(0)
      } 
    }); 

    if(this.navParams.data.messages){
      this.messages = this.navParams.data.messages.concat(this.messages)
      this.refreshDisplaytime()
      this.transHtml()
      this.ContentResize()
    }  
  }

  ionViewCanEnter(){
    if(this.session && this.session.userId && this.session.userId == '50709171857408' ){ 
      if(this.session.user.nickName == 'Beta'){  
        return true
      }else {
        this.nativeService.showToast(this.translateService.instant('APP_COMMON_DISABLE_FUCTION')); 
        return false
      } 
    }
  }


  ionViewDidLoad() { 
    this.keyboard.setResizeMode('body')
    this.nativeService.hideKeyboardAccessoryBar(true);
    this.toUser._id = this.navParams.data.widgetId || 'bot_001';
    this.session.messageWidgetId = this.navParams.data.widgetId || 'bot_001';
    this.toUser.pic = this.navParams.data.widgetIcon || this.toUser.pic;
    this.toUser.username = this.navParams.data.widgetName || 'bot';
 
    this.chatBox = '';  
    this.messageTitle = this.navParams.data.widgetName;

    this.user.pic = this.session.user ? this.session.user.pictureUrl : this.user.pic
    this.user._id = this.session.userId;  
  
    
    if(!this.navParams.data.messages){
      this.createArray();
    }
    this.events.subscribe("messageProvider-addMessage", message=>{
      if(this.messages.findIndex(p=>p.id == message.id) == -1){
        this.messages.push(message);
        this.transHtml();
        this.scrollToBottom()
        this.cd.detectChanges()
      } 
    }); 
  }
 
  ionViewWillUnload(){
    this.events.unsubscribe("messageProvider-addMessage")
  }

  lowerCaseFormatType(type: string){
    if(!type)return""
    return type.toLowerCase()
  }

  ionViewDidLeave() {
    this.keyboard.setResizeMode('')
    this.nativeService.hideKeyboardAccessoryBar(false);
  }
  /**
   * 取消输入，例如：点击除输入区域的其他地方
   */
  cancelTyping() {
    this.isShowChooser = false;
    // this.setFooterPosition(0)
    this.ContentResize()
    this.cd.detectChanges()
  }

  /**
   * 失焦時，
   */
  blurInput() { 
    setTimeout(() => {
      if (this.isSending || this.isShowChooser) return;
      // this.setFooterPosition(0)
      this.ContentResize()
      this.cd.detectChanges()
    }, 10) 
  }

  /**
   * 输入框获取到焦点时 
   */
  focusInput() {
    this.isShowChooser = false;  
  }


  ionViewWillEnter() {  
    this.content.scrollDownOnLoad = true
    this.content.scrollTop = 999999
    setTimeout(()=>{
      this.content.scrollDownOnLoad = true
      this.content.scrollTop = 9999999 
    },100) 
  }
 

  // 回车发送消息
  FormSubmit(message) {
    // this.myInput.setFocus();
    // this.sendMessage(message, 'text');
  }

  // 点击发送按钮发送消息，重新获取焦点
  send(message) { 
    if(!message || !message.replace(/\s+/g,""))return;
    this.isSending = true;
    
    this.sendMessage(message, 'text');
    this.myInput.setFocus();
    this.cd.detectChanges()
    setTimeout(() => { 
      this.isSending = false;
    }, 30)

  }

  /**
   * 发送图片信息
   * @param imageBase64 图片base64
   * @param abstract 描述
   * @param title 
   */
  sendImageMessage(scope: any,imageBase64: string, abstract: string, title?: string){
    (scope || this).sendMessage(abstract,'image',imageBase64);
  }
  /**
   * 发送消息
   * @param message 
   * @param formatType 消息类型: 'text':文本；'image':图片
   */
  sendMessage(message: string, formatType: string ,imageBase64?: string) { 
    if (message && message !== '') {

      const messageData: Message = {
        widgetId: this.toUser._id,
        id: this.session.userId + Date.now(),
        userId: this.user._id,
        widgetName: this.toUser.username,
        imageUrl: imageBase64,
        widgetIcon: this.toUser.pic,
        abstract: message,
        receiveTime: Date.now().toString(),
        formatType: formatType,
        isRead: true,
        isSend: true,
        status: this.messageProvider.STAUTS.SEND_MESSAGE_SENDING,
        extra:{
          groudId: 0,
          senderAvator: this.user.pic ,
          senderName: this.session.user.username , 
          senderId: Number(this.toUser._id),
          fromType:'Chat'
        }
      };
  
      this.messageProvider.insertMessage(messageData); 
      this._sendMessageFromMessageProvider(messageData);
      this.messages.push(messageData); 
      this.transHtml()
      this.scrollToBottom();
    }
    this.chatBox = '';
    this.refreshDisplaytime()
  }

  _sendMessageFromMessageProvider(messageData: Message){
    this.messageProvider.sendMessage(messageData).subscribe(result=>{
      let status = result ? this.messageProvider.STAUTS.SEND_MESSAGE_OK : this.messageProvider.STAUTS.SEND_MESSAGE_FAILED; 
      this._updateMessageStatus(status,messageData)  
    },err=>{
      let status = this.messageProvider.STAUTS.SEND_MESSAGE_FAILED; 
      this._updateMessageStatus(status,messageData) 
      
    });
  }

  _updateMessageStatus(status: string,messageData: Message){
    this.messageProvider.updateMessageStatus(status,messageData).subscribe(res=>{
      let index = this.messages.findIndex(p=> p.id == messageData.id)
      if(index > -1)this.messages[index].status = status;
    });
  }

  /**
   * 重新發送消息
   * @param message 
   */
  reSendMessage(message: Message){
 
      let confirm = this.alertCtrl.create({
        title: this.translateService.instant('MESSAGE_CHAT_RESEND'), 
        buttons: [
          {
            text: this.translateService.instant('MESSAGE_DELETE_CANCEL'),
            handler: () => {
            }
          },
          {
            text: this.translateService.instant('MESSAGE_DELETE_CONFIRM'),
            handler: () => {
              let index = this.messages.findIndex(p => p.id == message.id);
              if(index > -1){
                message.receiveTime = Date.now().toString()
                message.status = this.messageProvider.STAUTS.SEND_MESSAGE_SENDING;
                this.messages.splice(index,1)
                this.messages.push(message)
                this.refreshDisplaytime()
                this.messageProvider.updateMessage(message).subscribe(res=>{
                  this._sendMessageFromMessageProvider(message);
                });
              }
            }
          }
        ]
      });
      confirm.present();  

    
  }
  AlterSendMessage(messageData: Message, isSend?: boolean){
    
    this.messageProvider.insertMessage(messageData); 
    if(isSend){
      this.messageProvider.sendMessage(messageData).subscribe();
    }else{
      this.messageProvider.receiveMessageNew(messageData);
    } 
 
    this.messages.push(messageData); 
    this.transHtml()
    this.scrollToBottom();
  }
  /**
   * 滚动到底部
   */
  scrollToBottom(duration: number = 300) {
 
    setTimeout(() => {
      this.content.scrollToBottom(duration);
    }, 10);
  }
 
  setInputHeight(height: number = 36) {
    this.myInput._elementRef.nativeElement.style.height = height + 'px'
  }
  doRefresh(_event) {
    if (this.messages && this.messages.length > 0) this.topId = this.messages[0].id;
    this.createArray(_event)
 
  }
 
  refreshDisplaytime() {
    let i = 0;
    let timeline = null; // 3分钟作为一个时间轴
    for (let item of this.messages) {

      let receiveTime = Number(item.receiveTime);
      item.displayTime = Utility.formatMessageTime(this.session.language, receiveTime)
      if (i > 0) {
        let time = receiveTime;
        if (time - timeline > 1000 * 60 * 3) {
          timeline = time;
        } else {
          item.displayTime = ''
        }

      } else {
        timeline = receiveTime;
      }
      i++;
    }
  }

  scrollTopByHandle() {
    if(!this.topId)return;
    let ele = this.element.nativeElement.querySelector(`#messageid-${this.topId}`);
    this.content.scrollTop = ele.offsetTop > 20 ? ele.offsetTop - 20 : ele.offsetTop
  }
  transHtml() {
    for (let item of this.messages) {
      if (item.abstract) {
        item.displayAbstract = this.messageProvider.transformHtml(item.abstract);
        item.displayAbstract = this.sanitizer.bypassSecurityTrustHtml(item.displayAbstract); //遵循angular机制才会正常绑定html 
      }
    }
  }

  abstractOnClick(e, message: Message) {
    e.stopPropagation() //阻止冒泡事件 
    e = e || window.event; //兼容IE8
    let target = e.target || e.srcElement;  //判断目标事件 
    const url = target.href === 'javascript:void(0)' ? (target.title || target.innerText) : ''
    this.goMessageDetail(message, url)
  }


  // 长按弹出菜单
  onHold(message: Message) {
    let that = this;
    let sheet = null;

    let buttons = [];
    buttons.push({
      text: that.translateService.instant('MESSAGE_DELETE'),
      handler: () => {
        that.showDeleteMsg(message);
      }
    })

    if (message.redirectUrl || message.formatType.indexOf('Image') > -1) {

      buttons.push({
        text: that.translateService.instant('SHARE_MODAL_TITLE'),
        handler: () => {
          that.openSharePage(message);
        }
      })

    } else {
      buttons.push({
        text: that.translateService.instant('APP_COMMON_COPY_MESSAGE'),
        handler: () => {
          this.clipboard.copy(message.abstract)
        }
      })
    }

    buttons.push({
      text: that.translateService.instant('MESSAGE_DELETE_CANCEL'),
      role: 'cancel'
    })
     
    //长按时同时识别图片中是否存在二维码，若存在，则增加 识别二维码选项
    this.cacheProvider.decodeQRCode(message.imageUrl).subscribe(decodeResult => {
      buttons.splice(buttons.length - 1, 1, {
        text: that.translateService.instant('APP_COMMON_EXTRACT_QRCODE'),
        handler: () => {
          that.openQRCodeUrl(decodeResult, message);
        }
      })
      buttons.push({
        text: that.translateService.instant('MESSAGE_DELETE_CANCEL'),
        role: 'cancel'
      })

      let newsheet = that.actionSheetCtrl.create({ buttons: buttons });
      newsheet.present();
      sheet && sheet.dismiss()
    },err=>{
      sheet = that.actionSheetCtrl.create({ buttons: buttons });
      sheet.present();
     })
 
  }
  

  //显示删除alert
  showDeleteMsg(message) {
    let confirm = this.alertCtrl.create({
      title: this.translateService.instant('MESSAGE_DELETE_TITLE'),
      message: this.translateService.instant('MESSAGE_DELETE_MESSAGE'),
      buttons: [
        {
          text: this.translateService.instant('MESSAGE_DELETE_CANCEL'),
          handler: () => {
          }
        },
        {
          text: this.translateService.instant('MESSAGE_DELETE_CONFIRM'),
          handler: () => {
            let index = this.messages.findIndex(p => p.id == message.id);
            this.messages.splice(index, 1);
            this.messageProvider.deleteMessage(message.id); 
          }
        }
      ]
    });
    confirm.present();
  }

  /**
  * 打开分享页面
  */
 openSharePage(message: Message) {

  let shareMessage = {
    title: message.title, //标题
    description: message.abstract, //描述
    thumb: message.imageUrl || DEFAULT_LOGO, //缩略图
    resource: message.redirectUrl || message.imageUrl //图片url或网页url
  }


  this.preload.openShareModal({
    shareMessage: shareMessage,
    callback:(data)=>{
      if(data){ 
      }
    },
    isShareImage:message.redirectUrl ? false : true ,
    navigationDockTargetMapIds: [1, 2]
    // navigationDockTargetMapIds: [1, 2, 7]
  })
}

  /**
   * 打开二维码地址
   * @param imgMsg 二维码url
   * @param message Message对象
   */
  openQRCodeUrl(imgMsg, message) {
    let page: Webpage = {
      url: imgMsg,  // 链接地址
      title: message.title, // 标题
      description: message.abstract,   // 摘要内容
      imageUrl: message.imageUrl,   // 图片路径
      appId: message.widgetId.toString(),  // 應用ID
      options: {}
    };
    this.widgetProvider.openWebpage(page, this.navCtrl);
  }

  goMessageDetail(message: Message, replaceUrl?: string) {

    message.isRead = true;
    this.messageProvider.setMessageIsRead(message.id);
    if (!message.redirectUrl && !replaceUrl) return;
    let page: Webpage = {
      url: replaceUrl || message.redirectUrl,  // 链接地址
      title: message.title == null ? message.widgetName : message.title, // 标题
      description: message.abstract,   // 摘要内容
      imageUrl: message.imageUrl,   // 图片路径
      appId: message.widgetId == null ? '' : message.widgetId.toString(),  // 應用ID
      options:{}
    };
    this.widgetProvider.openWebpage(page, this.navCtrl, true);
  }
  getPictureByPhotoLibrary() {
    event.stopPropagation();
    if (this.nativeService.isMobile()) {
      this.nativeService.getFullPictureByPhotoLibrary({ 
      }).subscribe(img => {  
        this.nativeService.compressImage(img, { MaxWidthOrHeight: 1024 }).subscribe(res=>{
          this.sendImageMessage(this,res.dataURL,'Image')
        })
      }, (err) => {
        this.nativeService.showToast(err)
      });
    } else {
      Utility.generateQRCode("https://iproud.fihmb.com/Resources/Widgets/66295698871296.png").then(img=>{
        this.sendImageMessage(this,img,'Image')
      })
      
    }

  }

  getPictureByCamera() {
    event.stopPropagation();
    if (this.nativeService.isMobile()) {
      this.nativeService.getFullPictureByCamera({ 
      }).subscribe(img => {
        this.nativeService.compressImage(img, { MaxWidthOrHeight: 1024 }).subscribe(res=>{
          this.sendImageMessage(this,res.dataURL,'Image')
        })
        
      }, (err) => {
        this.nativeService.showToast(err)
      });
    } else {
      Utility.generateQRCode("https://iproud.fihmb.com/Resources/Widgets/66295698871296.png").then(img=>{
        this.sendImageMessage(this,img,'Image')
      })
    }
  }


  /**
   * 更多选项事件
   */
  showOptions() {
    // this.isTyping = true;
    // this.isShowChooser = !this.isShowChooser;
    // this.setFooterPosition(this.session.keyboardHeight || 243)
    // if (!this.isShowChooser) this.myInput.setFocus();
    this.cd.detectChanges()
    this.ContentResize()

    this.modal.create('MessageImagePickerPage',{ callback: this.sendImageMessage,scope: this},{
      enterAnimation: 'modal-from-bottom-enter',
      leaveAnimation: 'modal-from-bottom-leave',
      cssClass:'pick-modal'
    }).present()
  }


  ContentResize() {
    setTimeout(() => {
      this.content.resize();
      setTimeout(() => {
        this.scrollToBottom();
      }, 50)
    }, 50)
  }

  /**
   * 设置Footer的位置
   * @param paddingBottom 
   */
  setFooterPosition(paddingBottom: number|string) {    
    this.footer.nativeElement.style.setProperty('padding-bottom',`${paddingBottom}px`,'important'); 
    this.ContentResize()
    this.cd.detectChanges()
  }

  getFooterPaddingBottomHeight(): string{
    let style = this.footer.nativeElement.style;
    return style.getPropertyValue('padding-bottom');
  }



  /**
   * 点击图片放大
   * @param message 
   * @param _event 
   */
  clickOnImage(message,_event) {
    let ImageEvent = _event || event;
    ImageEvent.stopPropagation()
    let imageArray = [];

    for (let item of this.messages) { 
      if (item.formatType.toLowerCase() == 'image') {
        imageArray.push(
          {
            id: item.id,
            pictureUrl: item.imageUrl
          }
        )
      }
    }
    if(imageArray.length > 0){
      let index = imageArray.findIndex(p => p.id == message.id)
      this.preloadProvider.previewImage(imageArray, index,(data)=>{ 
      })
    }
   
  }

  /**
   * 打开用户信息页面
   * @param message 
   */
  openMemberPage(friendId: any) {
    this.navCtrl.push('ContactDetailPage', { friendId: friendId }) 
  }

  /**
   * 查询聊天记录且创建数据格式
   * @param _event 刷新事件
   */
  createArray(_event?) {  
    this.messageProvider.queryMessage(this.toUser._id, this.limit, this.messages.length || this.offset).subscribe(res => {
      if(res){ 
        this.messages = res.concat(this.messages)
        this.refreshDisplaytime()
        this.transHtml()

        setTimeout(() => {
          this.scrollTopByHandle()
        }, 10)
      }
    })
    _event && _event.complete()
  }


  shareContactCard(){
    this.preload.openContactChooserPage({ callback: this.AfterShare,type: 1 }); 
  }

  AfterShare(data){ 
  
      let contact = data[0];
      let confirm = this.alertCtrl.create({
        title:"Share contact card",
        message: data[0].name,
        buttons: [
          {
            text: this.translateService.instant('MESSAGE_DELETE_CANCEL'),
            handler: () => { 
            }
          },
          {
            text: this.translateService.instant('MESSAGE_DELETE_CONFIRM'),
            handler: () => {
                const messageData: Message = {
                  widgetId: this.toUser._id,
                  id: data[0].id,
                  userId: this.user._id,
                  widgetName: this.toUser.username,
                  imageUrl: data[0].avator,
                  widgetIcon: this.toUser.pic,
                  abstract: data[0].name,
                  receiveTime: Date.now().toString(),
                  formatType: "card",
                  isRead: true,
                  isSend: true,
                  status:'success' 
                };
                this.AlterSendMessage(messageData,true)            

            }
          }
        ]
      });
      confirm.present(); 
  }
}




