import { HandleProvider } from './../../providers/handle/handle';
import { CacheProvider } from './../../providers/providers';

import { Component, Input, ChangeDetectorRef, SimpleChanges } from '@angular/core';

import { NativeService, StorageService } from '../../services/services';
import { Events } from 'ionic-angular';
/**
 * Generated class for the ImgLazyLoadComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'img-lazy-load',
  templateUrl: 'img-lazy-load.html'
})
export class ImgLazyLoadComponent {

  default: string = '';//图片默认路径

  defaultImg: string = 'assets/svg/loading.svg';
  @Input() src: string //要显示的图片 
  @Input() isLoading: Boolean //要显示的图片 
  @Input() width: string //图片宽度
  @Input() height: string //图片高度 
  @Input() maxWidth: string //图片最大宽度
  @Input() maxHeight: string //图片最大高度 
  @Input() options: any //图片传入样式对象
  @Input() isKeepRatio: boolean //图片传入样式对象
  occupyHeight: string;
  constructor(public storageService: StorageService,
    public changeDetectorRef: ChangeDetectorRef,
    public nativeService: NativeService,
    public events: Events,
    public cache: CacheProvider,
    public handle: HandleProvider) { 


  }


  ngOnInit() {
    this._loadingImage()
  }

  _loadingImage() {
    if (!this.src) return;
    this.src = this.src.trim()
    if(this.src.startsWith('./assets') || this.src.startsWith('assets')){
      this.loadImg(this.src);
    }else if (this.src && this.nativeService.isMobile()) {
      this.cache.getImagePath(this.src).subscribe((res) => {
        this.handle.generate('getImageFromLocal',0,res)
        this.loadImg(res);
      }, (err) => {
        this.loadImg(this.src);
      })
    } else {
      this.loadImg(this.src);
    }
  }

  /**
   * 当src地址发生变化，刷新组件
   * @param changes 
   */
  ngOnChanges(changes: SimpleChanges) {
    if(!changes.src.firstChange && changes.src.currentValue){
      this.src = changes.src.currentValue
      this._loadingImage() 
    } 
  }
  imgStyleOptions() {
    let styleObject = {} 
    if(this.options){
      if(this.options instanceof Object){
        styleObject = this.options
      }else{
        styleObject = JSON.parse(this.options)
      }
     
    }
    if(this.width){
      styleObject['width'] = this.width;
    }
    if(this.height) {
      styleObject['height'] = this.height;
    }
    if(this.maxWidth){
      styleObject['max-width'] = this.maxWidth;
    }
    if(this.maxHeight) {
      styleObject['max-height'] = this.maxHeight;
    } 
    return styleObject
    
  }

  vitrualImgStyleOptions() {
    let styleObject = {} 
    if(this.options){
      if(this.options instanceof Object){
        styleObject = this.options
      }else{
        styleObject = JSON.parse(this.options)
      }
     
    }
    if(this.width){
      styleObject['width'] = this.width;
    }
    if(this.height || this.occupyHeight) {
      styleObject['height'] =  this.height || this.occupyHeight;
    }
    if(this.maxWidth){
      styleObject['max-width'] = this.maxWidth;
    }
    if(this.maxHeight) {
      styleObject['max-height'] = this.maxHeight;
    } 
    return styleObject 
  }
  //加载标签图片
  loadImg(src) {
  
    if (!src) return;
    let img = new Image();
    img.src = src;
    this.occupyHeight = img.height + 'px' //未加载到图片时，给定一个高度占位。
    img.onload = () => { 
      this.default = img.src;
      if (!this.changeDetectorRef['destroyed']) {
        this.changeDetectorRef.detectChanges();
      } 
    }
  }

}



