import { NgModule } from '@angular/core';
import { ImgLazyLoadComponent } from './img-lazy-load/img-lazy-load';
import { TranslateModule } from '@ngx-translate/core';
import { DataHintsComponent } from './data-hints/data-hints';
import { AvatarBadgeComponent } from './avatar-badge/avatar-badge'
import { PrivacyPolicyComponent } from './privacy-policy/privacy-policy';
import { NodataComponent } from './nodata/nodata';
import { AlphaListComponent } from './alpha-list/alpha-list';
import { AlphaSectionComponent } from './alpha-list/alpha-section';
import { CommonModule } from '@angular/common';
import { AlphaCellComponent } from './alpha-list/alpha-cell';
import { CountoComponent } from './counto/counto';
import { IproudLoadDataComponent } from './iproud-load-data/iproud-load-data';

@NgModule({
    declarations: [
        ImgLazyLoadComponent, AvatarBadgeComponent,
        DataHintsComponent,
        PrivacyPolicyComponent,
        NodataComponent,
        AlphaListComponent,
        AlphaSectionComponent,
        AlphaCellComponent,
    CountoComponent,
    IproudLoadDataComponent
    ],
    imports: [
        CommonModule,
        TranslateModule.forChild()
    ],
    exports: [
        ImgLazyLoadComponent,
        DataHintsComponent, 
        AvatarBadgeComponent,
        PrivacyPolicyComponent,
        NodataComponent,
        AlphaListComponent,
        AlphaSectionComponent,
        AlphaCellComponent,
    CountoComponent,
    IproudLoadDataComponent
    ]
})
export class ComponentsModule { }



