import { Component, Input, SimpleChanges } from '@angular/core';

/**
 * Generated class for the CountoComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'counto',
  templateUrl: 'counto.html'
})
export class CountoComponent  {
  displayVal: number = 0;
  @Input() endVal: number;

  @Input() miles : number = 1000;

  constructor() {
    this.startAnimation()
  }

  startAnimation(delval?: number){ 
    let miles = this.endVal - this.displayVal;  
    if(delval){
      miles = delval
    } 
    if(miles > 0){ 
      let rasio = Math.round(Math.abs(miles) * 10 / this.miles)  || 1 
      let numSlideFun = () => setTimeout(()=>{ 
        this.displayVal += rasio;
          if(this.displayVal >= this.endVal){
            this.displayVal = this.endVal  
          }else{  
            numSlideFun()
          }
      },0)
      numSlideFun()
    } else if(miles < 0){ 
      let rasio = Math.round(Math.abs(miles) * 10 / this.miles ) || 1 
      let numSlideFun = () => setTimeout(()=>{
        this.displayVal -= rasio; 
        if(this.displayVal <= this.endVal){
          this.displayVal = this.endVal   
        }else{ 
          numSlideFun()
        } 
      },0)
      numSlideFun()
    }
  }

  ngOnChanges(changes: SimpleChanges) { 
    this.endVal = changes.endVal.currentValue; 
    this.startAnimation(changes.endVal.currentValue - changes.endVal.previousValue)
  }
}



