import { LoadDataButton } from './../../models/component';
import { Utility } from './../../services/utility';
import { Session } from './../../services/session';
import { Events } from 'ionic-angular';
import { Component, ChangeDetectorRef, Input, Output, EventEmitter, SimpleChanges } from '@angular/core'; 
import { TranslateService } from '@ngx-translate/core';
 
/**
 * Generated class for the IproudLoadDataComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'iproud-load-data',
  templateUrl: 'iproud-load-data.html'
})
export class IproudLoadDataComponent { 
  @Input() isDataEmpty: boolean;
  @Input() isDataLoading: boolean;
  @Input() withEmptyImage: boolean = true;
  @Input() text: string;
  @Input() translateKey: string = ''; 
  @Input() Buttons: Array<LoadDataButton>; 
  @Output() emitter = new EventEmitter();
  showText: string;
  imgUrl: string;  
  DataEmpty: boolean = false;
  DataLoading: boolean = false; 
  buttons: Array<LoadDataButton>; 
  constructor(public events: Events, public session: Session,private cd: ChangeDetectorRef,public translate: TranslateService) { 
    this.events.subscribe('setting:change-theme', () => { 
      setTimeout(()=>{ 
        this.imgUrl = Utility.boolean( this.withEmptyImage) ? Utility.getThemeColor(this.session.isDarkMode).NO_MESSAGE : null;
      },200)
    }) 
    this.events.subscribe('lang:changed', (obj) => { 
      setTimeout(()=>{  
        this.showText = this.translateKey ? this.translate.instant(this.translateKey) : this.translate.instant('APP_COMMON_NODATA');
        this.cd.detectChanges(); 
      },100)
    })
  }
  
  ngOnInit() {
    this.DataEmpty = this.isDataEmpty;
    this.DataLoading =this.isDataLoading;
    this.imgUrl =  Utility.boolean( this.withEmptyImage) ? Utility.getThemeColor(this.session.isDarkMode).NO_MESSAGE : null;
    this.showText = this.translateKey ? this.translate.instant(this.translateKey) : this.translate.instant('APP_COMMON_NODATA');
    this.buttons = this.Buttons; 
  } 

  ngOnChanges(changes: SimpleChanges) {  
    this.isDataEmpty = changes.isDataEmpty && changes.isDataEmpty.currentValue;  
    this.isDataLoading = changes.isDataLoading && changes.isDataLoading.currentValue;  
    this.imgUrl =  Utility.boolean( this.withEmptyImage) ? Utility.getThemeColor(this.session.isDarkMode).NO_MESSAGE : null;
    this.showText = this.translateKey ? this.translate.instant(this.translateKey) : this.translate.instant('APP_COMMON_NODATA');

    this.DataEmpty = this.isDataEmpty;
    this.DataLoading =this.isDataLoading;  
    this.buttons = this.Buttons; 
    this.cd.detectChanges()
  }

  buttonOnClick(event: LoadDataButton ){
    this.emitter.emit({
      eventName: event.name,
      func: event.func,
      args: event.args
    });
  }
}



