import { TranslateService } from '@ngx-translate/core';
import { Session } from './../../services/session';
import { Events } from 'ionic-angular';
import { Component, Input ,ChangeDetectorRef } from '@angular/core';
import { Utility } from '../../services/utility';

/**
 * Generated class for the NodataComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */


@Component({
  selector: 'nodata',
  templateUrl: 'nodata.html'
})
export class NodataComponent {
  @Input() text: string;

  @Input() translateKey: string = '';
  showText: string;
  imgUrl: string;
  constructor(public events: Events, public session: Session,private cd: ChangeDetectorRef,public translate: TranslateService) {
    this.events.subscribe('setting:change-theme', () => {

      setTimeout(()=>{ 
        this.imgUrl = Utility.getThemeColor(this.session.isDarkMode).NO_MESSAGE;
      },200)
    })


    this.events.subscribe('lang:changed', (obj) => { 
      setTimeout(()=>{  
        this.showText = this.translateKey ? this.translate.instant(this.translateKey): this.text;
        this.cd.detectChanges(); 
      },100)
    })

  }
  ngOnInit() {
    this.imgUrl = Utility.getThemeColor(this.session.isDarkMode).NO_MESSAGE;
    this.showText = this.translateKey ? this.translate.instant(this.translateKey): this.text;
  } 
 
}



